body {
  margin: 0;
  padding: 0;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*---------- Global Style ----------*/

body {
  color: #222;
  font-family: "Ubuntu", sans-serif !important;
  font-size: 17px;
}

.ubuf {
  font-family: "Ubuntu", sans-serif;
}

html {
  overflow-x: hidden;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Ubuntu", sans-serif;
  font-weight: 200;
  color: #111;
  line-height: 1.5em;
  margin-top: 0;
}
p {
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  margin-top: 1px;
}
p,
ul,
li {
  color: #666;
}
section {
  padding-top: 185px;
  padding-bottom: 185px;
}
.no-gutter {
  padding-right: 0;
  padding-left: 0;
}
a,
a:link {
  color: #666;
}
a:hover,
a:focus,
a:visited {
  color: #666;
  text-decoration: none;
}
.vcenter {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
}
.fill {
  height: 100vh;
}
.margin-top-05 {
  margin-top: 0.5em;
}
.margin-top-10 {
  margin-top: 1em;
}
.margin-top-15 {
  margin-top: 1.5em;
}
.margin-top-20 {
  margin-top: 2em;
}
.margin-top-25 {
  margin-top: 2.5em;
}
.margin-top-30 {
  margin-top: 3em;
}
.margin-top-35 {
  margin-top: 3.5em;
}
.margin-top-40 {
  margin-top: 4em;
}
.margin-top-45 {
  margin-top: 4.5em;
}

/*---------- Navbar ----------*/

.nav {
  position: relative;
  width: auto;
  /* display: inline-block; */
  border: none;
  float: right;
  z-index: 99999;
}
.navbar-fixed-top {
  padding-top: 10px;
}
.navbar-brand {
  padding-top: 25px;
}
.navbar-brand > img {
  max-width: 127px;
  z-index: 9999;
}
@media (max-width: 768px) {
  .navbar-brand {
    padding-left: 0px;
  }
}
.navbar-default {
  background-color: transparent;
  border-color: transparent;
}
.navbar-default.color-fill {
  background-color: #fff;
  border-color: transparent;
}
.btn-nav {
  background: transparent;
  border: none;
  padding: 15px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  cursor: pointer;
  z-index: 99999;
}
@media (max-width: 768px) {
  .btn-nav {
    padding: 15px 0;
  }
}
.btn-nav:focus {
  outline: 0;
}
.icon-bar {
  display: block;
  margin: 6px 0;
  width: 30px;
  height: 3px;
  background-color: #222;
}
.icon-bar.inverted {
  background-color: #666;
}
@media (max-width: 992px) {
  .icon-bar.inverted {
    background-color: #222;
  }
}
.opaque {
  background-color: #fff !important;
}
.btn-nav:hover .icon-bar {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  background-color: #555;
}
.nav-content {
  position: fixed;
  top: -100%;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 1);
  display: block;
  height: 100%;
  z-index: 9;
}
.nav-list {
  list-style: none;
  padding: 0;
  position: relative;
}
.item-anchor {
  color: #fff;
  font-size: 30px;
  font-weight: 100;
  position: relative;
  text-decoration: none;
  padding: 10px;
}
.item-anchor:hover,
.item-anchor:focus {
  color: #ffffff;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.item-anchor:hover:after,
.item-anchor:focus:after {
  width: 100%;
  background: #f7f7f7;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.nav-item {
  font-family: "Ubuntu", sans-serif;
  margin: 10px auto;
  text-align: center;
  color: #222;
}
.nav-item a:link,
.nav-item a:hover,
.nav-item a:focus {
  color: #222;
}
.nav-item a:hover,
.nav-item a:focus {
  border-bottom: 2px solid #222;
}
.animated {
  display: block;
  margin: 0 auto;
}
.animated:hover .icon-bar,
.animated:focus .icon-bar {
  background-color: #fff;
}
.animated:focus {
  cursor: pointer;
  z-index: 9999;
}
.middle {
  margin: 0 auto;
}
.icon-bar {
  -webkit-transition: all 0.7s ease;
  transition: all 0.7s ease;
  z-index: 999999;
}
.animated .icon-bar {
  z-index: 999999;
  background-color: #222 !important;
}
.animated .top {
  -webkit-transform: translateY(7px) rotateZ(45deg);
  transform: translateY(7px) rotateZ(45deg);
}
.animated .bottom {
  -webkit-transform: translateY(-11px) rotateZ(-45deg);
  transform: translateY(-11px) rotateZ(-45deg);
}
.animated .middle {
  width: 0;
}
@keyframes showNav {
  from {
    top: -100%;
  }
  to {
    top: 0;
  }
}
@-webkit-keyframes showNav {
  from {
    top: -100%;
  }
  to {
    top: 0;
  }
}
.showNav {
  -webkit-animation: showNav 1s ease forwards;
  animation: showNav 1s ease forwards;
}
@keyframes hideNav {
  from {
    top: 0;
  }
  to {
    top: -100%;
  }
}
@-webkit-keyframes hideNav {
  from {
    top: 0;
  }
  to {
    top: -100%;
  }
}
.hideNav {
  -webkit-animation: hideNav 1s ease forwards;
  animation: hideNav 1s ease forwards;
}
.hidden {
  display: none;
}

/*---------- Menu Icon Animation ----------*/

.navbar-toggle * {
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.navbar-toggle {
  border: medium none;
  border-radius: 0;
  color: #fff;
  cursor: pointer;
  height: 50px;
  padding: 16px;
  width: 50px;
  float: right;
  margin-top: 0px;
  margin-bottom: 0px;
}
.navbar-default .navbar-toggle:hover,
.navbar-default .navbar-toggle:focus {
  background-color: #fff;
}
.navbar-default .navbar-toggle,
.navbar-default .navbar-collapse,
.navbar-default .navbar-form {
  border-color: #fff;
}
.navbar-toggle span {
  width: 25px;
  height: 3px;
  margin-bottom: 5px;
  background-color: #222;
  display: block;
}
.navbar-toggle span.bar1 {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transform-origin: 1px 3px;
  transform-origin: 1px 3px;
  width: 31px;
}
.navbar-toggle span.bar2 {
  -webkit-transform: rotate(-360deg) scale(0);
  transform: rotate(-360deg) scale(0);
}
.navbar-toggle span.bar3 {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transform-origin: 8px 2px;
  transform-origin: 8px 2px;
  width: 31px;
}
.navbar-toggle.collapsed span.bar1,
.navbar-toggle.collapsed span.bar2,
.navbar-toggle.collapsed span.bar3,
.navbar-toggle.collapsed span.bar4 {
  -webkit-transform: none;
  transform: none;
  width: 30px;
}

/*---------- Header ----------*/

#header {
  height: 50%;
  margin: 0 auto;
  position: relative;
  color: #fff;
  text-align: center;
  background-size: cover;
}
#headslide {
  position: relative;
}
#headslide .img-responsive,
#headslide .thumbnail > img,
#headslide .thumbnail a > img,
#headslide .carousel-inner > .item > img,
#headslide .carousel-inner > .item > a > img {
  height: 100vh;
  max-width: none;
  object-fit: cover;
  width: 100%;
}
#header h1,
#header p {
  color: #fff;
}
#header h1 {
  font-size: 3.5em;
}
#header p {
  font-size: 1.5em;
}

/*---------- Portfolio ----------*/

#portfolio {
  padding-top: 185px;
}
.background-image-holder {
  background-attachment: scroll;
  background-clip: border-box;
  background-origin: padding-box;
  background-position: 50% 50% !important;
  background-repeat: repeat;
  background-size: cover !important;
  height: 100%;
  left: 0;
  top: 0;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  width: 100%;
  z-index: 1;
}
.background-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.square-thumbs li {
  height: 285px;
}
.lightbox-grid li {
  padding: 8px;
  float: left;
  opacity: 1;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
@media (max-width: 992px) {
  .lightbox-grid li {
    padding: 8px 0;
  }
}
.lightbox-grid,
.lightbox-grid ul,
.lightbox-grid li {
  overflow: hidden;
  position: relative;
}
.portfolio-sorting {
  padding-left: 15px;
  font-family: "Ubuntu", sans-serif;
}
.portfolio-sorting li a:hover,
.portfolio-sorting li a.active {
  border-bottom: 2px solid #222;
}
.portfolio-item {
  margin-bottom: 30px;
}
.item {
  height: 100vh;
}
.item img {
  width: 100%;
  height: 100vh !important;
  object-fit: cover;
}

.profilepic {
  height: 20%;
  width: 20%;
}

.intro {
  color: #111;
  font-size: 2em;
}

.footer {
  align-content: center;
  margin-bottom: 10px;
}

.sicon {
  padding: 0.8em;
}

.mail {
  height: 5%;
  width: 5%;
}

.cfooter {
  margin-top: 30px;
}

.dpimage {
  width: 60%;
  margin-bottom: 10px;
  border-radius: 50%;
}
.line {
  line-height: 10px;
}

